﻿@import "../../../base/fn";
@import "weui_select";

.weui_select_before {
    padding-right: $weuiCellGapH;
    .weui_select{
        width:auto;
    }
    .weui_cell_hd {
        position: relative;
        &:after {
            @include setRightLine($weuiCellBorderColor);
        }
        &:before{
            content: " ";
            @include setArrow_Wap(right, 6px, #C8C8CD, 2px);

            position: absolute;
            top: 50%;
            right: $weuiCellGapH;
            margin-top: -3px;
        }
    }
    .weui_cell_bd {
        padding-left: $weuiCellGapH;
        &:after{
            display:none;
        }
    }
}